<template>
	<view class="box">
		<view class="withdraw-url">
			<text class="iconfont iconjilu"></text>
			提现记录
		</view>
		<view class="money-box">
			<view class="money-li">
				<view class="money width100">{{activeJfValue | formatNum}}</view>
				<view class="text width100">活动积分</view>
				<!-- <view class="bg-btn visi-hidden">提现</view> -->
			</view>
			<view class="money-li">
				<view class="money width100">{{incomeJfValue | formatNum}}</view>
				<view class="text width100">收益积分</view>
				<!-- <view class="bg-btn ">提现</view> -->
			</view>
		</view>
		<view class="money-box">
			<view class="money-li">
				<view class="money width100">{{goldBeanValue | formatNum}}</view>
				<view class="text width100">金豆</view>
				<!-- <view class="bg-btn visi-hidden">提现</view> -->
			</view>
			<view class="money-li">
				<view class="money width100">{{goldCoinValue | formatNum}}</view>
				<view class="text width100">金币</view>
				<!-- <view class="bg-btn ">提现</view> -->
			</view>
		</view>
		<view class="money-box">
			<view class="money-li">
				<view class="money width100">{{cashValue | formatNum}}</view>
				<view class="text width100">我的佣金</view>
			<navigator :url="'/pages/member/withdraw?type=3&money='+cashValue" class="bg-btn">提现</navigator>
			</view>
			<view class="money-li">
				<view class="money width100">0</view>
				<view class="text width100">区域代理收益</view>
				<!-- <view class="bg-btn ">提现</view> -->
			</view>
		</view>
		<view class="money-box">
			<view class="money-li" style="border-bottom:none ;">
				<view class="money width100">{{merchantCashValue | formatNum}}</view>
				<view class="text width100">店铺可提现总额</view>
				<navigator :url="'/pages/member/withdraw?type=5&money='+merchantCashValue" class="bg-btn">提现</navigator>
			</view>

		</view>
	</view>
</template>

<script>
	//1:金币提现
	//2:收益积分提现    
	//3:佣金提现
	//4:区域代理提现
	//5:店铺可提现总额
	export default {
		data() {
			return {
				"activeJfValue": 0, //活动积分
				"incomeJfValue": 0, //收益积分
				"couponValue": 0, //团批券
				"cashValue": 0, //我的佣金
				"merchantCashValue": 0, //商家可提现金额
				"goldBeanValue": 0, //金豆余额
				"goldCoinValue": 0 //金币余额
			}
		},
		onLoad() {
			this.getMoney()
		},
		methods: {
			getMoney() {
				this.$api.get('/o2oCustomerCash/getAccountBalanceBySheetId', {
					params: {
						customerSeqId: this.$common.getLocalSync('customerSeqId')
					}
				}).then(res => {
					let data = res.data;
					if (res.code == 200) {
						this.merchantCashValue = data.merchantCashValue; //可提现金额
						this.couponValue = data.couponValue; //团批劵
						this.goldBeanValue = data.goldBeanValue; //金豆余额
						this.activeJfValue=data.activeJfValue
						this.incomeJfValue=data.incomeJfValue
						this.cashValue=data.cashValue
						this.goldCoinValue=data.goldCoinValue
					}
				})
			},
		}
	}
</script>

<style lang="scss">
	.visi-hidden {
		visibility: hidden;
	}

	.withdraw-url {
		.iconfont {
			margin-right: 4rpx;
			color: #FF9E97;
		}

		font-size:26rpx;
		color: #666666;
		width: calc(100% - 68rpx);
		text-align: right;
		margin: 0 auto;
		padding: 20rpx 0;
	}

	.box {
		width: 100%;
		min-height: calc(100vh - var(--window-top));
		background: white;

		.money-box {
			padding: 0 34rpx;
			width: calc(100% - 68rpx);
			margin: 0 auto;
			display: flex;

			.money-li {
				width: 49%;
				height: 210rpx;
				display: flex;
				align-content: center;
				justify-content: center;
				flex-wrap: wrap;
				flex-shrink: 0;
				font-size: 26rpx;
				border-bottom: 1px solid rgba(238, 238, 238, 0.8);

				.money {
					color: #F6591E;
					font-size: 30rpx;

				}

				.text {
					font-size: 26rpx;
					line-height: 2.4;
				}

				.bg-btn {
					background: rgba(255, 237, 236, 1);
					width: auto;
					color: #F9867E;
					padding: 10rpx 30rpx;
				}

				.width100 {
					width: 100%;
					text-align: center;
				}
			}

			.money-li:first-child {
				border-right: 1px solid rgba(238, 238, 238, 0.8);
			}
		}
	}
</style>
